          </svg>
          <slot></slot>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["data", "extra"],
  name: "",
  data() {
    return {};
  },
  methods: {}
};
</script>

<style lang='scss'>
.InfoComp {
  .head {
    $mar: 0%;
    $pad: 0%;
    font-weight: bold;
    font-size: 1.5rem;
    margin-left: $mar;
    width: 100% - $mar;
    padding-left: $pad;
    padding-right: $pad;
    padding-bottom: 0.3rem;
    margin-top: 1rem;
    &.headborder {
      border-bottom: 3px solid #63aeeb;
    }

    td {
      white-space: nowrap;
      &:nth-child(1) {
        width: 40%;
      }
      &:nth-child(2) {
        // width: 40%;
      }
      &:nth-child(3) {
        text-align: right;
      }
    }
  }
  > div:nth-child(1) .head {
    margin-top: 0;
  }
  .contents {
    font-size: 1.4rem;
    padding: 0.3rem;
    > div {
      display: flex;
      padding: 0.1rem;
      margin-left: 1rem;
      .icon {
        width: 1.2rem;
        height: 1.2rem;
        color: #1e8ccb;
        margin-top: -0.4rem;
      }
      > span {
      }
    }
  }
}
</style>


